/**
 * Music
 */
import { observable, computed, autorun } from "mobx"
import Http from "../utils/http"
class ObservableStore {
    constructor() { }
    // MV
    @observable MVList = JSON.parse(window.localStorage.getItem("MVList")) || {};
    // 
    @observable current = {};
    /**
    * 
    * getMusic 
    * getLyric 
    * 
    */
    async play(id?) {
        this.current = {};
        await this.getMV(id);
        let MV = { ...this.MVList[id] };
        MV.brs = Object.keys(MV.brs).map(x => {
            return { brs: x, url: Http.address + "mv/url?url=" + MV.brs[x] }
        })
        // 
        this.current = MV;
    }
    /**
     *  MV
     * http
     * @param id /mv?mvid=5436712
     */
    async getMV(id: string) {
        // 
        let res: any = {};
        let data = {};
        if (!this.MVList.hasOwnProperty(id)) {
            res = await Http.get(`mv?mvid=` + id);
            data[id] = res.data;
        } else {
            res = this.MVList[id];
        }
        this.MVList = { ...this.MVList, ...data };
        window.localStorage.setItem("MVList", JSON.stringify(this.MVList));
        return res;
    }

}
const Store = new ObservableStore();
console.log("Store----MV", Store);
export default Store;





